import React from 'react';
import { serverResponse, getData } from './request';


class App extends React.Component {
    constructor (props) {
    		super(props);
    		this.state = {
    			data: null
    		};
    		this.onSendData = this.onSendData.bind(this);
	}

    onSendData(value,name) {
		// this.setState({ data: serverResponse(getData(value,name)) });
    	this.state.data = serverResponse(getData(value,name));
        console.log(this.state.data);
	}

    render() {
        console.log(this.state.data);
        return (
            <React.Fragment>
                <div className="general">
                    <div className="top">
                        <div className="chart">
                            6
                        </div>
                        <div className="order">
                            {this.state.data}
                        </div>
                    </div>
                    <div className="bottom">
                        <div className="content">
                            <button onClick={(e)=>{this.onSendData('11','token1')}}>Отправить</button>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default App;
